.App {
  background: linear-gradient(90deg, #1d4350, #a43931);
  height: 100vh;
  
  display: grid;
}

.memory-game {
  height: 90vh;
  width: 60vh;
  display: flex;
  flex-wrap: wrap;
  justify-content:  'center';
  margin: auto;
}

.memory-card {
  width: 17vh;
  height: 17vh;
  position: relative;
  margin: 5px;
  transform: scale(1);
}
.memory-card:active {
  transform: scale(0.97);
  transition: transform 0.2s;
}
.front-face,
.back-face {
  position: absolute;
  width: 100%;
  height: 100%;
}
.card-value {
  margin: auto;

  height: 100%;
  width: 100%;
}

.back-face {
  display: flex;
}
